<template>
  <view class="container">
    <wd-toast />
    <view class="content">
      <!-- ==================== 标题区域 ==================== -->
      <view class="title">What are you look for today?</view>
      <!-- ==================== 四个选项 ==================== -->
      <view
        v-for="(item, index) in optionsList"
        :key="index"
        class="option-item"
        @click="handleClickOption(item)"
      >
        <view class="option">
          <view class="icon">
            <image :src="item.icon" mode="widthFix" style="width: 70%" />
          </view>
          <view class="text">{{ item.text }}</view>
          <view class="arrow">
            <image :src="item.arrowIcon" mode="widthFix" />
          </view>
        </view>
      </view>
    </view>
    <custom-tabbar />
  </view>
</template>

<script setup>
import { ref } from "vue"
// 导入必要的图片
import RequestArrowRight from "@/static/common/r-arrow-right.svg"
import OfferArrowRight from "@/static/common/o-arrow-right.svg"

import img_offeringServices from "@/static/request/4.png"
import img_offeringGoods from "@/static/request/5.png"

import img_requestServices from "@/static/request/1.png"
import img_requestingGoodss from "@/static/request/requestingGoodss.png"

import { useNavigate, flattenToNestedObject } from "@/hooks/useNavigate"
import CustomTabbar from "@/components/CustomTabbar.vue"

// 页面数据加载
onLoad(async (options) => {
  console.log("------------------------- {request} --------------------------")
  const NestedObjectOptions = flattenToNestedObject(options)
  console.log("NestedObjectOptions", NestedObjectOptions)
})

// ==================================== 路由跳转相关 ====================================
/**
 * 执行页面跳转逻辑
 * @param {object} option - 跳转目标索引（1-4对应不同页面类型）
 * @description 根据类型跳转到指定页面：
 * 1=Offering services, 2=Offering goods,
 * 3=Requesting services, 4=Requesting goods
 */
const handleClickOption = (option) => {
  useNavigate("/pages/request/category", {
    method: option.method,
    type: option.type,
  })
}

/**
 * 页面选项配置数据
 * @type {Array<{
 *   type: number,
 *   icon: string,
 *   text: string,
 *   arrowIcon: string
 * }>}
 * @description 选项数据结构说明:
 * - type: 选项类型标识(1-4)
 * - icon: 选项图标路径
 * - text: 选项显示文本
 * - arrowIcon: 右侧箭头图标路径
 */
const optionsList = ref([
  {
    type: 1,
    method: 1,
    icon: img_offeringServices,
    text: "Offering services",
    arrowIcon: OfferArrowRight,
  },
  {
    type: 1,
    method: 2,
    icon: img_offeringGoods,
    text: "Offering goods",
    arrowIcon: OfferArrowRight,
  },
  {
    type: 2,
    method: 3,
    icon: img_requestServices,
    text: "Requesting services",
    arrowIcon: RequestArrowRight,
  },
  {
    type: 2,
    method: 4,
    icon: img_requestingGoodss,
    text: "Requesting goods",
    arrowIcon: RequestArrowRight,
  },
])
</script>

